body{
  display: flex;
  flex-direction: center;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-image: url("https://wallpapershome.com/images/pages/pic_h/19547.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.card{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 30%;
  width: 80%;
  background-color: whitesmoke;
  color: black;
  font-size: 15px;
  font-weight: bold;
  /* margin: 10px; */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 14px 10px 7px 6px rgba(9,13,4,1);
}
button{
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  background-color: lightgrey;
  color: black;
  font-weight: bolder;
  padding: 10px;
  border-radius: 50px;
  margin-top: 10px;
  font-size: 14px;
  border-style: none;
  border-color: azure;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
}
button:hover{
  animation: rotate 0.8s ease-in-out;
}

@keyframes rotate{
   0%{
     transform: rotate(0deg);
   }
   25%{
    transform: rotate(3deg);
  }
  50%{
    transform: rotate(-3deg);
  }
  75%{
    transform: rotate(1deg);
  }
  100%{
    transform: rotate(0deg);
  }
}